* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country__item {
  display: flex;
  position: relative;
}

.country__flag {
  position: absolute;
  bottom: 15px;
  display: block;
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.country__name {
  display: block;
  font-size: 20px;
  margin-left: 50px;
}

.countryCard__title {
  display: flex;
  align-items: center;
  margin-bottom: -4px;
}

.countryCard__flag {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.countryCard__name {
  font-size: 36px;
  font-weight: bold;
}

.countryCard__value {
  font-size: 20px;
}

.countryCard__label {
  font-weight: bold;
}
